import { useRef } from 'react'
import { useSpring, animated } from 'react-spring'
import theme from '../../theme'

const RawStandardInput = () => {
  const spring = useSpring({ value: 100, from: { value: 0 } })
  return (
    <>
      <input type="text" />
      <style jsx>{`
        input {
          border: ${spring.value * 0.01}px solid ${theme.palette.shade[30]};
          border-radius: 3px;
          padding: ${theme.spacing}px ${theme.spacing * 2}px;
          outline: none;
        }
      `}</style>
    </>
  )
}
const StandardInput = animated(RawStandardInput)

export default StandardInput
